"use client";

import React, { useState, useEffect } from "react";
import { JobSearchFilters } from "@/components/job-search-filters";
import { JobListingCard } from "@/components/job-listing-card";
import { JobService, Job } from "@/services/jobService";

interface JobSearchFilters {
  searchQuery: string;
  locations: string[];
  remoteOptions: ("remote" | "hybrid" | "onsite")[];
  jobTypes: ("full-time" | "part-time" | "contract")[];
  salaryRange: { min: number; max: number };
  techStack: string[];
}

export default function Home() {
  const [filters, setFilters] = useState<JobSearchFilters | null>(null);
  const [searchQuery, setSearchQuery] = useState("");
  const [savedJobs, setSavedJobs] = useState<Set<string>>(new Set());
  const [jobs, setJobs] = useState<Job[]>([]);
  const [loading, setLoading] = useState(true);

  // Load jobs from Supabase on component mount
  useEffect(() => {
    loadJobs();
  }, []);

  const loadJobs = async () => {
    setLoading(true);
    try {
      const jobsData = await JobService.getJobs();
      setJobs(jobsData);
    } catch (error) {
      console.error("Failed to load jobs:", error);
    } finally {
      setLoading(false);
    }
  };

  // Search jobs when search query changes
  useEffect(() => {
    if (searchQuery.trim()) {
      searchJobs(searchQuery);
    } else {
      loadJobs();
    }
  }, [searchQuery]);

  const searchJobs = async (query: string) => {
    setLoading(true);
    try {
      const jobsData = await JobService.searchJobs(query);
      setJobs(jobsData);
    } catch (error) {
      console.error("Failed to search jobs:", error);
    } finally {
      setLoading(false);
    }
  };

  const handleSaveJob = (id: string, saved: boolean) => {
    setSavedJobs((prev) => {
      const newSet = new Set(prev);
      if (saved) {
        newSet.add(id);
      } else {
        newSet.delete(id);
      }
      return newSet;
    });
  };

  const handleApplyJob = (id: string) => {
    const job = jobs.find((j) => j.id === id);
    if (job) {
      // You can add application tracking logic here
      window.open("#", "_blank"); // Replace with actual application URL
    }
  };

  // Transform Supabase data to match your component props
  const transformJobData = (job: Job) => ({
    id: job.id,
    companyName: job.companies?.name || "Unknown Company",
    companyLogo: job.companies?.logo_url || "/placeholder-company-logo.jpg",
    jobTitle: job.title,
    jobType: job.job_type as "full-time" | "part-time" | "contract",
    remoteLevel: job.remote_level as "remote" | "hybrid" | "onsite",
    location: job.location,
    salaryMin: job.salary_min || 0,
    salaryMax: job.salary_max || 0,
    salaryPeriod: "year" as const, // Adjust based on your data
    techStack: job.required_tech_stack || [],
    postedDate: new Date(job.posting_date || job.created_at),
    source: "Database", // You can track sources in your schema
    sourceUrl: "#", // Add application_url to your schema if needed
  });

  const filteredJobs = jobs.map(transformJobData).filter((job) => {
    if (!filters) return true;

    // Location filter
    if (
      filters.locations.length &&
      !filters.locations.some((loc) =>
        job.location.toLowerCase().includes(loc.toLowerCase())
      )
    ) {
      return false;
    }

    // Remote options filter
    if (
      filters.remoteOptions.length &&
      !filters.remoteOptions.includes(job.remoteLevel)
    ) {
      return false;
    }

    // Job types filter
    if (filters.jobTypes.length && !filters.jobTypes.includes(job.jobType)) {
      return false;
    }

    // Salary range filter
    if (filters.salaryRange) {
      const jobSalaryMin =
        job.salaryPeriod === "year"
          ? job.salaryMin
          : (job.salaryMin || 0) * 2000;
      if (
        jobSalaryMin < filters.salaryRange.min ||
        jobSalaryMin > filters.salaryRange.max
      ) {
        return false;
      }
    }

    // Tech stack filter
    if (filters.techStack.length) {
      const hasMatchingTech = filters.techStack.some((tech) =>
        job.techStack.some((t) => t.toLowerCase() === tech.toLowerCase())
      );
      if (!hasMatchingTech) {
        return false;
      }
    }

    return true;
  });

  return (
    <main className="min-h-screen bg-background">
      <div className="mx-auto max-w-6xl px-4 py-12">
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-foreground md:text-5xl">
            Find Your Dream Job
          </h1>
          <p className="mt-2 text-lg text-muted-foreground">
            Advanced filtering to help you discover the perfect opportunity
          </p>
        </div>

        {/* Job Search Filters */}
        <JobSearchFilters
          onFilterChange={setFilters}
          onSearch={setSearchQuery}
        />

        {/* Job Listings Grid */}
        <div className="mt-12">
          {loading ? (
            <div className="text-center py-12">
              <p className="text-lg text-muted-foreground">Loading jobs...</p>
            </div>
          ) : filteredJobs.length > 0 ? (
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-semibold text-foreground">
                  {filteredJobs.length}{" "}
                  {filteredJobs.length === 1 ? "Job" : "Jobs"} Found
                </h2>
                {savedJobs.size > 0 && (
                  <span className="text-sm text-muted-foreground">
                    {savedJobs.size} saved
                  </span>
                )}
              </div>
              <div className="grid gap-4">
                {filteredJobs.map((job) => (
                  <JobListingCard
                    key={job.id}
                    {...job}
                    isSaved={savedJobs.has(job.id)}
                    onSave={handleSaveJob}
                    onApply={handleApplyJob}
                  />
                ))}
              </div>
            </div>
          ) : (
            <div className="rounded-lg border border-input bg-card p-12 text-center">
              <p className="text-lg text-muted-foreground">
                No jobs found matching your criteria
              </p>
              <p className="mt-2 text-sm text-muted-foreground">
                Try adjusting your filters to see more results
              </p>
            </div>
          )}
        </div>
      </div>
    </main>
  );
}
