'use client'

import React, { useState } from 'react'
import { MapPin, DollarSign, Bookmark, BookmarkCheck, ExternalLink, Briefcase, Clock, Globe, Building2 } from 'lucide-react'
import { Card, CardContent, CardFooter } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { cn } from '@/lib/utils'

// TypeScript Interfaces
export interface JobListingCardProps {
  id: string
  companyName: string
  companyLogo: string
  jobTitle: string
  jobType: 'full-time' | 'part-time' | 'contract'
  remoteLevel: 'remote' | 'hybrid' | 'onsite'
  location: string
  salaryMin?: number
  salaryMax?: number
  salaryPeriod?: 'year' | 'month' | 'hour'
  techStack: string[]
  postedDate: Date | string
  source: string
  sourceUrl?: string
  isSaved?: boolean
  onSave?: (id: string, saved: boolean) => void
  onApply?: (id: string) => void
}

// Badge Component for Job Type and Remote Level
interface BadgeProps {
  variant: 'job-type' | 'remote'
  children: React.ReactNode
}

const Badge: React.FC<BadgeProps> = ({ variant, children }) => {
  const baseClasses =
    'inline-flex items-center gap-1 px-2.5 py-1 rounded-md text-xs font-medium'

  if (variant === 'job-type') {
    return (
      <span
        className={cn(
          baseClasses,
          'bg-blue-100 text-blue-700 dark:bg-blue-900/30 dark:text-blue-400',
        )}
      >
        <Briefcase className="size-3" />
        {children}
      </span>
    )
  }

  if (variant === 'remote') {
    return (
      <span
        className={cn(
          baseClasses,
          'bg-emerald-100 text-emerald-700 dark:bg-emerald-900/30 dark:text-emerald-400',
        )}
      >
        <Globe className="size-3" />
        {children}
      </span>
    )
  }

  return null
}

// Format salary for display
const formatSalary = (
  min?: number,
  max?: number,
  period: 'year' | 'month' | 'hour' = 'year',
): string => {
  if (!min && !max) return ''

  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  })

  const minStr = min ? formatter.format(min) : ''
  const maxStr = max ? formatter.format(max) : ''

  const range = minStr && maxStr ? `${minStr} - ${maxStr}` : minStr || maxStr

  const periodLabel =
    period === 'hour' ? '/hr' : period === 'month' ? '/mo' : '/yr'

  return `${range}${periodLabel}`
}

// Format date to relative time
const formatDate = (date: Date | string): string => {
  const d = typeof date === 'string' ? new Date(date) : date
  const now = new Date()
  const diffTime = Math.abs(now.getTime() - d.getTime())
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))

  if (diffDays === 0) return 'Today'
  if (diffDays === 1) return 'Yesterday'
  if (diffDays < 7) return `${diffDays}d ago`
  if (diffDays < 30) return `${Math.floor(diffDays / 7)}w ago`
  return `${Math.floor(diffDays / 30)}mo ago`
}

// Main Job Listing Card Component
export const JobListingCard: React.FC<JobListingCardProps> = ({
  id,
  companyName,
  companyLogo,
  jobTitle,
  jobType,
  remoteLevel,
  location,
  salaryMin,
  salaryMax,
  salaryPeriod = 'year',
  techStack,
  postedDate,
  source,
  sourceUrl,
  isSaved = false,
  onSave,
  onApply,
}) => {
  const [saved, setSaved] = useState(isSaved)

  const handleSave = () => {
    const newState = !saved
    setSaved(newState)
    onSave?.(id, newState)
  }

  const jobTypeLabel =
    jobType === 'full-time'
      ? 'Full-time'
      : jobType === 'part-time'
        ? 'Part-time'
        : 'Contract'

  const remoteLabel =
    remoteLevel === 'remote'
      ? 'Remote'
      : remoteLevel === 'hybrid'
        ? 'Hybrid'
        : 'On-site'

  const salaryDisplay = formatSalary(salaryMin, salaryMax, salaryPeriod)
  const timeAgo = formatDate(postedDate)

  return (
    <Card className="group relative overflow-hidden transition-all duration-300 hover:shadow-lg hover:scale-[1.01]">
      {/* Card Header with Company Logo and Title Section */}
      <CardContent className="pt-6">
        <div className="flex gap-4">
          {/* Company Logo */}
          <div className="flex-shrink-0">
            <img
              src={companyLogo || "/placeholder.svg"}
              alt={companyName}
              className="size-12 rounded-lg object-cover ring-1 ring-border"
            />
          </div>

          {/* Main Content Area */}
          <div className="flex-1 min-w-0">
            {/* Company Name and Save Button */}
            <div className="flex items-start justify-between gap-2">
              <div className="min-w-0 flex-1">
                <p className="text-xs font-medium text-muted-foreground mb-1">
                  {companyName}
                </p>
                <h3 className="text-lg font-semibold text-foreground mb-3 line-clamp-2">
                  {jobTitle}
                </h3>
              </div>

              {/* Save Button */}
              <button
                onClick={handleSave}
                className="flex-shrink-0 p-1.5 rounded-lg hover:bg-accent transition-colors"
                aria-label={saved ? 'Remove from saved' : 'Save job'}
              >
                {saved ? (
                  <BookmarkCheck className="size-5 fill-primary text-primary" />
                ) : (
                  <Bookmark className="size-5 text-muted-foreground hover:text-foreground" />
                )}
              </button>
            </div>

            {/* Badges */}
            <div className="flex flex-wrap gap-2 mb-3">
              <Badge variant="job-type">{jobTypeLabel}</Badge>
              <Badge variant="remote">{remoteLabel}</Badge>
            </div>

            {/* Location and Salary */}
            <div className="grid grid-cols-2 gap-3 mb-3 text-sm">
              <div className="flex items-center gap-1.5 text-muted-foreground">
                <MapPin className="size-4 flex-shrink-0" />
                <span className="truncate">{location}</span>
              </div>

              {salaryDisplay && (
                <div className="flex items-center gap-1.5 text-muted-foreground">
                  <DollarSign className="size-4 flex-shrink-0" />
                  <span className="truncate">{salaryDisplay}</span>
                </div>
              )}
            </div>

            {/* Tech Stack Tags */}
            <div className="flex flex-wrap gap-1.5 mb-3">
              {techStack.slice(0, 4).map((tech) => (
                <span
                  key={tech}
                  className="inline-flex px-2 py-1 rounded-md bg-secondary text-secondary-foreground text-xs font-medium"
                >
                  {tech}
                </span>
              ))}
              {techStack.length > 4 && (
                <span className="inline-flex px-2 py-1 rounded-md bg-secondary text-secondary-foreground text-xs font-medium">
                  +{techStack.length - 4}
                </span>
              )}
            </div>
          </div>
        </div>
      </CardContent>

      {/* Card Footer with Date, Source, and Actions */}
      <CardFooter className="flex items-center justify-between gap-2 border-t pt-4">
        <div className="flex items-center gap-4 text-xs text-muted-foreground">
          {/* Posted Date */}
          <div className="flex items-center gap-1">
            <Clock className="size-3.5" />
            <span>{timeAgo}</span>
          </div>

          {/* Source */}
          <div className="flex items-center gap-1">
            <Building2 className="size-3.5" />
            <span>{source}</span>
          </div>
        </div>

        {/* Action Buttons */}
        <div className="flex gap-2">
          {sourceUrl && (
            <Button
              variant="outline"
              size="sm"
              asChild
              className="gap-1.5"
            >
              <a href={sourceUrl} target="_blank" rel="noopener noreferrer">
                <ExternalLink className="size-3.5" />
                <span className="hidden sm:inline">View</span>
              </a>
            </Button>
          )}
          <Button
            variant="default"
            size="sm"
            onClick={() => onApply?.(id)}
            className="gap-1.5"
          >
            <ExternalLink className="size-3.5" />
            <span className="hidden sm:inline">Apply</span>
          </Button>
        </div>
      </CardFooter>
    </Card>
  )
}
